
import Datetime from 'vux/src/components/datetime/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'

import { mapState } from "vuex";
import { JDGetSAPPurchaseOrder,JDGetSAPPurchaseOrderExcel } from "@/api/getData";
import Vue from "vue";
Vue.use(DatetimePlugin);
const date = new Date
const dateToday = date.getFullYear() + '-' + ((date.getMonth() + 1) < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1) )+ '-' + ((date.getDate() ) < 10 ? '0' + (date.getDate() ) : (date.getDate()) )
export default {
data() {
return {
form: {
EBELN:"",
WeixinOpenID: "",
ZQY:"",
VBELN:"",
ZZXTNO:"",
ZXT:"",
BeginDate: dateToday,
EndDate: dateToday
},
show: false,
BeginDate: "",
EndDate: "",
visibility: false,
visibility2: false,
widths: 100,
tableHead: [
"厂区",
"采购订单号",
"行项目",
"计划行",
"确认交期",
"物料编码",
"物料描述",
"销售订单",
"销售订单行项目",
"型体",
"分配型体",
"单位",
"订单数量",
"送货数量",
"已暂收数量",
"合格数量",
"不合格数量",
"短码数量",
"入库数量",
"退货数量"
],
orderList: []
};
},
computed: {
...mapState({
openId: state => state.user.userInfo
})
},
components: {
Datetime
},
mounted() {
if (this.getSSionStorage('order')) {
this.form.BeginDate = JSON.parse(this.getSSionStorage('order')).split(',')[0]
this.form.EndDate = JSON.parse(this.getSSionStorage('order')).split(',')[1]
}
this.form.WeixinOpenID = this.openId;
this.search();





console.log(this.openId)
console.log(this.form);
this.widths = 6 * this.widths + "px";
console.log($(window).width());
},
methods: {

async search() {
this.recordSearch()
this.$store.commit("updateLoadingStatus", { isLoading: true });
try {
let res = await JDGetSAPPurchaseOrder(this.form);

this.$store.commit("SAVE_ORDERLIST", res);
this.$nextTick(() => {
this.$emit("init");
});
} catch (error) {
} finally {
this.$emit("close");
this.$store.commit("updateLoadingStatus", { isLoading: false });
}
},
async searchExcel() {
this.recordSearch()
this.$store.commit("updateLoadingStatus", { isLoading: true });
try {
console.log("导出");
let res = await JDGetSAPPurchaseOrderExcel(this.form);
console.log("--开始--");
console.log(res);
location.href = res;
console.log("--结束--");

this.$store.commit("SAVE_ORDERLIST", eval(res));
this.$nextTick(() => {
this.$emit("init");
this.$emit("close");

});
this.$store.commit("updateLoadingStatus", { isLoading: false });
} catch (error) {
if (error === "无数据") {
this.$store.commit("SAVE_ORDERLIST", []);
}
this.$emit("close");
this.$store.commit("updateLoadingStatus", { isLoading: false });

} finally {
this.$emit('close')
this.$store.commit("updateLoadingStatus", { isLoading: false });
}
console.log(this.form)
},

recordSearch() {
this.setSSionStorage('order', `${this.form.BeginDate},${this.form.EndDate}`)
},

showPlugin() {
let _this = this;
this.$vux.datetime.show({
cancelText: "取消",
confirmText: "确定",
format: "YYYY-MM-DD",
value: dateToday,
onConfirm(val) {
console.log("plugin confirm", val);
_this.form.BeginDate = val;
},
onShow() {
console.log("plugin show");
},
onHide() {
console.log("plugin hide");
}
});
},

showPlugin2() {
let _this = this;
this.$vux.datetime.show({
cancelText: "取消",
confirmText: "确定",
format: "YYYY-MM-DD",
value: dateToday,
onConfirm(val) {
_this.form.EndDate = val;
console.log("plugin confirm", val);
},
onShow() {
console.log("plugin show");
},
onHide() {
console.log("plugin hide");
}
});
},
onConfirm() {},
log() {},
popup() {
alert("0");
},
change() {}
}
};



