
import Search from "@/components/search.vue";
import orderSearch from "./search.vue";
import $ from "jquery";
import XTable from 'vux/src/components/x-table/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapGetter, mapState } from "vuex";
export default {
data() {
return {
show: false,
BeginDate: "",
EndDate: "",
visibility: false,
visibility2: false,
widths: 120,
tableHead: [
"厂区",
"采购订单号",
"行项目",
"计划行",
"确认交期",
"物料编码",
"物料描述",
"销售订单",
"销售订单行项目",
"型体",
"分配型体",
"单位",
"订单数量",
"送货数量",
"已暂收数量",
"合格数量",
"不合格数量",
"短码数量",
"入库数量",
"退货数量"
]
};
},
computed: {
...mapState({
orderList: state => state.mutaions.orderList
})
},
directives: {
TransferDom
},
components: {
XTable,
Datetime,
Group,
Search,
Popup,
orderSearch
},
mounted() {
console.log(this.$store);
this.widths = this.tableHead.length * this.widths + "px";
console.log($(window).width());
},
methods: {
vueTouch(e, index) {
console.log(e);
console.log(index);
this.$store.commit("SAVE_PROCHECKINFO", this.orderList[index]);
this.$router.push({ path: "/productcheck" });
},

close() {
this.show = false;
},
getOrderList(res) {
this.orderList = res;
},
log() {},
popup() {
alert("0");
},
toInfo() {
this.$router.push({ path: "/orderInfo" });
},
change() {},
init() {

var win = $(window),
scrollAreaEl = $(".t_r_content"),
leftFreezeEl = $(".t_l_freeze"),
leftTableEl = leftFreezeEl.find("table"),
rightTableEl = $(".t_r_t table");


function adjustHeight() {
var winHeight = win.height(),
tableHeight = winHeight - 90;
leftFreezeEl.height(tableHeight);
scrollAreaEl.height(tableHeight);
}

adjustHeight();
win.on("resize", adjustHeight);


var myScroll = new IScroll(".t_r_content", {
scrollX: true,
scrollY: true,
probeType: 3
});


scrollAreaEl.on("touchmove mousewheel", function(e) {
e.preventDefault();
});


myScroll.on("scroll", updatePosition);
myScroll.on("scrollEnd", updatePosition);

function updatePosition() {
var a = this.y;
var b = this.x;
leftTableEl.css(
"transform",
"translate(0px, " + a + "px) translateZ(0px)"
);
rightTableEl.css(
"transform",
"translate(" + b + "px, 0px) translateZ(0px)"
);
}
}
}
};



